import React from "react";
/* Import Custom Components */
import { Login, Cpanel, Config, Database, NotFound } from "../pages";
import {
  listCODEMODEcollection,
  listGRAPHICMODEcollection,
  createCODEMODEcollection,
  createGRAPHICMODEcollection,
  addCODEMODEdocument,
  addGRAPHICMODEdocument
} from "../pages/collections";

const Dummie = props => <div>Hola Dummie!</div>;
const routes = [
  {
    path: "/",
    exact: true,
    component: Config
  },
  {
    path: "/cpanel",
    component: Cpanel,
    routes: [
      { path: "/cpanel", component: Dummie, exact: true },
      { path: "/cpanel/collection/add", component: addCODEMODEdocument },
      {
        path: "/cpanel/collection/create",
        component: createCODEMODEcollection
      },
      { path: "/cpanel/collections", component: listCODEMODEcollection }
    ]
  },
  {
    path: "/login",
    component: Login
  },
  {
    path: "/database",
    component: Database
  },

  {
    component: NotFound
  }
];

export default routes;
