import React from "react";
import { BrowserRouter as Router, Switch } from "react-router-dom";
import { withRouter } from "react-router-dom";
import { Layout, Menu, Icon } from "antd";
import qs from "qs";
/* Import Custom Components */
import "./cpanelLayout.scss";
import Header from "../../components/header/panel/panelHeader";
import Footer from "../../components/footer/Footer";
import { Helper } from "../../utils";
let { RouteWithSubRoutes } = Helper;

const { Content, Sider } = Layout;
const LogoLarge = require("../../resources/images/logo.svg");
const LogoSmall = require("../../resources/images/logo-recostado.svg");
const cpanelLayout = props => {
  let routes = props.routes || [];
  let { className, location } = props;
  let { search } = location;
  let query = qs.parse(search.replace("?", ""));
  let theme = query.theme || "light";
  return (
    <Router>
      <Layout className={`main-layout ${className || ""} ${theme}`}>
        <Sider collapsible className="side-bar">
          <div className="logo">
            <img id={"logo-large"} src={LogoLarge} />
            <img id={"logo-small"} src={LogoSmall} />
            <span className="logo-title">DonecLab</span>
          </div>
          <Menu
            className="menu-sider"
            theme={"light"}
            defaultSelectedKeys={["1"]}
            mode="inline"
          >
            <Menu.Item key="1">
              <Icon type="dashboard" />
              <span>Dashboard</span>
            </Menu.Item>
            <Menu.Item key="2">
              <Icon type="database" />
              <span>Collections</span>
            </Menu.Item>
            <Menu.Item key="3">
              <Icon type="api" />
              <span>Plugins</span>
            </Menu.Item>
            <Menu.Item key="4">
              <Icon type="rise" />
              <span>Connection</span>
            </Menu.Item>
            <Menu.Item key="5">
              <Icon type="setting" />
              <span>Settings</span>
            </Menu.Item>
          </Menu>
        </Sider>
        <Layout>
          <Header />
          <Content className="main-content">
            <Switch>
              {routes.map((route, index) => {
                console.log("ROUTE: ", route);
                return <RouteWithSubRoutes key={index} {...route} />;
              })}
            </Switch>
          </Content>
          <Footer />
        </Layout>
      </Layout>
    </Router>
  );
};
export default withRouter(cpanelLayout);
