import React from "react";
import { Route } from "react-router-dom";
export default {
  parseFormData: function(params) {
    const formData = new FormData();
    for (var s in params) {
      formData.append(s, params[s]);
    }
    return formData;
  },
  RouteWithSubRoutes: function(route) {
    return (
      <Route
        path={route.path}
        render={props => {
          let parent = typeof route.routes != "undefined";
          return (
            // pass the sub-routes down to keep nesting
            <route.component {...props} routes={route.routes} parent={parent} />
          );
        }}
      />
    );
  }
};
