import React, { Component } from "react";
/* Redux Connect */
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import actions from "../../redux/actions";

/* Import Custom Components */
import Layout from "../../layouts/cpanel/cpanelLayout";
import { Row, Col, Icon, Button, Input } from "antd";

class Cpanel extends Component {
  componentDidMount() {
    const { actions } = this.props;
    const theme = this.props.match.params.theme;
    theme !== "light" && theme !== "dark"
      ? (this.themeColor = "light")
      : (this.themeColor = theme);
    actions.action_search(["1", "2", "3"]);
  }
  componentWillReceiveProps(nextProps) {
    let { data } = nextProps;
    console.log("Example Reducers Data: ", data);
  }

  render() {
    let routes = this.props.routes || [];
    return <Layout  routes={routes} themeColor={this.themeColor} />;
  }
}
/* Mapping Actions to Props*/
const mapStateToProps = state => {
  const { data } = state;
  return {
    data
  };
};
/* Event Action Dispatch to Props */
const mapDispatchToProps = dispatch => ({
  actions: bindActionCreators(actions, dispatch)
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Cpanel);
